import { updateDocument } from "api/collections";
import { queryClient } from "index";
import { Command } from "utils/classes/Command";
import { Document } from "utils/interfaces";

export class UpdateDocument extends Command {
  data: Document;
  newData: any;
  collectionId: string;

  constructor(collectionId: string, data: Document, newData: any) {
    super();
    this.collectionId = collectionId;
    this.data = data;
    this.newData = newData;
  }

  async undo() {
    await updateDocument(this.collectionId, this.data.docId, this.data);
    await queryClient.invalidateQueries(this.collectionId);
  }
  async execute() {
    await updateDocument(this.collectionId, this.data.docId, this.newData);
    await queryClient.invalidateQueries(this.collectionId);
  }
}
