class LoadingManagerClass {
  loader: HTMLElement | undefined;

  constructor() {
    let interval = setInterval(() => {
      let el = document.getElementById("loading-indicator");
      if (el) {
        clearInterval(interval);
        this.loader = el;
      }
    }, 100);
  }

  show() {
    this.loader?.classList.remove("-translate-y-14");
  }

  hide() {
    this.loader?.classList.add("-translate-y-14");
  }
}

const LoadingManager = new LoadingManagerClass();

export default LoadingManager;
