import ErrorRounded from "@mui/icons-material/ErrorRounded";
import Tooltip from "@mui/material/Tooltip";
import React, { InputHTMLAttributes } from "react";
import { GroundType } from "utils/types";

const WarningIcon = React.forwardRef(function MyComponent(props: any, ref: any) {
  return (
    <div {...props} ref={ref}>
      <ErrorRounded className="text-red-500 text-xl" fontSize="inherit"></ErrorRounded>
    </div>
  );
});

export interface InputProps extends InputHTMLAttributes<HTMLInputElement> {
  className?: string;
  error?: string | boolean;
  ground: GroundType;
}

const Input: React.FC<InputProps> = ({ className = "", error, ground, ...props }) => {
  const classes = {
    base: `input${ground}Background input${ground}HoverBackground input${ground}HoverTextColor input${ground}FocusedBorderColor input${ground}FocusedBackground input${ground}FocusedTextColor`,
    error: "border-red-FF0000",
  };

  return (
    <div className={"relative flex items-center " + className}>
      <input
        className={`px-3 focus:border-2 h-full border-solid w-full placeholder-gray-500 rounded outline-none ${
          error ? classes.error : classes.base
        }`}
        {...props}
        spellCheck={false}
      ></input>
      {error ? (
        <Tooltip title={error} placement="top">
          <WarningIcon className="absolute right-2 my-auto w-auto"></WarningIcon>
        </Tooltip>
      ) : null}
    </div>
  );
};

export default Input;
