import { Command } from "utils/classes/Command";
import LoadingManager from "facades/LoadingManager";

export class CommandsController {
  history: Command[] = [];
  lastCommand: number = -1;
  busy = false;

  async execute(command: Command) {
    if (!this.busy) {
      this.busy = true;
      this.history = this.history.slice(0, this.lastCommand + 1);
      this.history.push(command);
      this.lastCommand++;
      await this.history[this.lastCommand].execute();
      this.busy = false;
    }
  }
  async undo() {
    if (!this.busy) {
      this.busy = true;
      LoadingManager.show();
      if (this.lastCommand >= 0) {
        await this.history[this.lastCommand].undo();
        this.lastCommand--;
      }
      LoadingManager.hide();
      this.busy = false;
    }
  }

  async redo() {
    if (!this.busy) {
      this.busy = true;
      LoadingManager.show();
      if (this.lastCommand < this.history.length - 1) {
        this.lastCommand++;
        await this.history[this.lastCommand].execute();
      }
      LoadingManager.hide();
      this.busy = false;
    }
  }
}
