import Input from "components/Input";
import Logo from "assets/logo.svg";
import Button from "components/Button";
import { useFormik } from "formik";
import * as yup from "yup";
import { RootState } from "store";
import { useNotify } from "components/NotificationsProvider";
import { useSelector } from "react-redux";
import { TasksManager } from "facades/TasksManager";

export interface LoginProps {}

const Login: React.FC<LoginProps> = () => {
  const notify = useNotify();
  const logo = useSelector(
    (state: RootState) => state.appearance.items.find((x) => x.id === "logo")?.value
  );

  const { values, errors, submitCount, handleSubmit, handleChange, isSubmitting } = useFormik({
    onSubmit: async (vals) => {
      await TasksManager._login(vals, notify);
    },
    initialValues: {
      password: "",
      email: "",
    },
    validationSchema: yup.object().shape({
      password: yup.string().required(),
      email: yup.string().email().required(),
    }),
  });
  return (
    <div className="fixed left-0 top-0 w-full h-full flex overflow-auto p-sm-12 p-7 loginBackground">
      <img alt="" src={logo || Logo} className="absolute left-7 top-5"></img>
      <div style={{ maxWidth: 380 }} className="m-auto w-full md:py-10 py-16">
        <form
          onSubmit={handleSubmit}
          className="LoginBoxBackground LoginBoxTextColor rounded-lg p-9 text-sm"
        >
          <div className="text-center mb-7">
            <div className="mb-2 text-27px font-medium">Login</div>
          </div>
          <div className="mb-7">
            <div className="mb-2">Email</div>
            <Input
              ground="Login"
              className="h-34px"
              name="email"
              value={values.email}
              onChange={handleChange}
              error={submitCount > 0 && errors.email ? errors.email : false}
            ></Input>
          </div>
          <div className="mb-9">
            <div className="mb-2">Password</div>
            <Input
              ground="Login"
              className="h-34px"
              type="password"
              name="password"
              value={values.password}
              onChange={handleChange}
              error={submitCount > 0 && errors.password ? errors.password : false}
            ></Input>
          </div>
          <Button type="submit" className="primaryBackground buttonPrimaryTextColor w-full h-34px">
            {isSubmitting ? "Loading..." : "Login"}
          </Button>
        </form>
      </div>
    </div>
  );
};

export default Login;
