const LoadingIndicator = () => {
  return (
    <div
      id="loading-indicator"
      className="fixed left-0 top-1 pointer-events-none w-full flex justify-center transition transform -translate-y-14"
    >
      <div className="px-7 py-3 rounded-md bg-white shadow-lg">Loading...</div>
    </div>
  );
};

export default LoadingIndicator;
