import KeyboardArrowLeftRounded from "@mui/icons-material/KeyboardArrowLeftRounded";
import { useHistory, matchPath, useLocation } from "react-router-dom";
import store, { RootState } from "store";
import ButtonBase from "@mui/material/ButtonBase";
import { useSelector } from "react-redux";
import CollectionsManager from "facades/CollectionsManager";
import Undo from "@mui/icons-material/Undo";
import Redo from "@mui/icons-material/Redo";
import Tooltip from "@mui/material/Tooltip";

const TopBar = () => {
  const history = useHistory();
  const location = useLocation();

  const match = matchPath<{ id: string }>(location.pathname, {
    path: ["/:id", "/collections/:id", "/collections/:id/Add", "/collections/:id/edit/:docId"],
    exact: true,
    strict: false,
  });

  let collectionName =
    useSelector(
      (state: RootState) => state.collectionTypes.find((x) => x.id === match?.params.id)?.name
    ) ||
    match?.params.id ||
    "";

  return (
    <>
      <div className="h-34px hidden md:flex topBarBackground topBarTextColor text-sm">
        <div className="flex-grow flex items-center">
          {/* <Tooltip title="Back" classes={{ tooltip: "font-poppins bg-fireck-4 text-black" }}> */}
          <ButtonBase
            onClick={() => history.goBack()}
            className="h-full px-2 topBarHoverBackground topBarHoverTextColor inline-flex items-center cursor-pointer outline-none"
          >
            <KeyboardArrowLeftRounded
              fontSize="inherit"
              className="text-2xl"
            ></KeyboardArrowLeftRounded>
          </ButtonBase>
          {/* </Tooltip> */}
          {!collectionName ? null : (
            <span className="capitalize px-3 rounded-md bg-black bg-opacity-5 py-0.5 mx-4">
              {decodeURI(collectionName)}
            </span>
          )}

          {/* <Tooltip title="Undo" classes={{ tooltip: "font-poppins bg-fireck-4 text-black" }}> */}
          <ButtonBase
            onClick={() => CollectionsManager.undo()}
            className="px-7 topBarHoverBackground topBarHoverTextColor cursor-pointer flex items-center outline-none h-full"
          >
            {/* <Undo fontSize="inherit" className="text-lg"></Undo> */}
            Undo
          </ButtonBase>
          {/* </Tooltip> */}
          {/* <Tooltip title="Redo" classes={{ tooltip: "font-poppins bg-fireck-4 text-black" }}> */}
          <ButtonBase
            onClick={() => CollectionsManager.redo()}
            className="px-7 topBarHoverBackground topBarHoverTextColor cursor-pointer flex items-center outline-none h-full"
          >
            {/* <Redo fontSize="inherit" className="text-lg"></Redo> */}
            Redo
          </ButtonBase>
          {/* </Tooltip> */}
        </div>
        <ButtonBase
          onClick={() => window.open("https://fireck.com/docs", "_blank")}
          className="px-9 topBarHoverBackground topBarHoverTextColor cursor-pointer flex items-center outline-none"
        >
          Docs
        </ButtonBase>
        <ButtonBase
          className="flex items-center px-9 topBarHoverBackground topBarHoverTextColor cursor-pointer outline-none"
          onClick={() =>
            store.dispatch({ type: "SET_USER", payload: { username: "", token: "", email: "" } })
          }
        >
          Logout
        </ButtonBase>
      </div>
    </>
  );
};

export default TopBar;
