import { Document } from "utils/interfaces";
import { CommandsController } from "utils/classes/CommandsController";
import { AddDocument } from "commands/AddDocument";
import { UpdateDocument } from "commands/UpdateDocument";
import { DeleteDocument } from "commands/DeleteDocument";
import { DeleteMultipleDocuments } from "commands/DeleteMultipleDocuments";
import { GetCollectionOptions } from "utils/interfaces";
import { getCollection } from "api/collections";

export class CollectionsManagerClass {
  controller = new CommandsController();

  undo() {
    this.controller.undo();
  }
  redo() {
    this.controller.redo();
  }

  addDocument(collectionId: string, data: any) {
    return this.controller.execute(new AddDocument(collectionId, data));
  }

  deleteDocument(collectionId: string, docId: string) {
    this.controller.execute(new DeleteDocument(collectionId, docId));
  }

  deleteMultipleDocuments(collectionId: string, documents: Document[]) {
    this.controller.execute(new DeleteMultipleDocuments(collectionId, documents));
  }

  updateDocument(collectionId: string, document: Document, newData: any) {
    return this.controller.execute(new UpdateDocument(collectionId, document, newData));
  }

  getCollection(options: GetCollectionOptions, signal?: AbortSignal) {
    const { collectionId } = options;
    return getCollection({ ...options }, signal);
  }
}

const CollectionsManager = new CollectionsManagerClass();

export default CollectionsManager;
